.contact {
    height: 550px;
    margin-top: 115px;
}

.contactHeader{
    padding: auto auto;
    font-size: 56px;
}

.contactLink{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 12px 48px;
    color: #ffffff;
    background: linear-gradient(to right, #0091aa 0, white 10%, #0091aa 20%);
    background-position: 0;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: shine 3s infinite linear;
    animation-fill-mode: forwards;
    -webkit-text-size-adjust: none;
    font-weight: 600;
    font-size: 94px;
    text-decoration: none;
    white-space: nowrap;
    &:hover{
        font-size: 128px;
        transition: 0.11s step-end;
    }

    @media (min-width: 768px) and (max-width: 1168px){
      margin-top: 85px;
    }
    @media (max-width: 568px){
      margin-top: 435px;
    }
}

.contactText{
  margin-top: 205px;
  padding-inline: 150px;
  color:#fff;

  @media (max-width: 1168px){
    margin-top: 235;
    padding-inline:35px;
  }
}

iframe{
    display: relative;
    margin-top: 100px;
    border:none;
    height: 650px;
    //border-radius: 85px;
    margin-inline: auto;
    width: 950px;
    z-index: 0;
    margin-bottom: 15px;
    overflow: hidden;
}

@keyframes shine{
  0%{
    background-position: 0
  }
  60%{
    background-position: 180px
  }
  100%{
    background-position: 290px
  }
}